
.item-look {
    float: left;
    background: transparent;
    padding: 15px;
    //width: 33%;

    z-index: 1;
    position: relative;
    @include md {
        width: 48%; }
    @include sm {
        width: 100%; }

    &__row {
        min-height: 55px;
        display: block;
        width: 100%;
        margin-bottom: 15px;
        position: relative; }
    .img-block {
        @include pseudo;
        text-align: center;
        position: relative;
        width: 100%;
        display: table;
        img {
            width: 100%;
            height: auto; } }


    &__title {
        font-size: 12px;
        font-weight: normal;
        color: #949494;
        line-height: 14px;
        height: 16px;
        overflow: hidden;
        text-transform: uppercase;
        @include md {
            height: 30px; }
        a {
            color: #949494; } } }


.page__catalog {}
.owl-gallery {
    .item {
        width: 100%;
 } }        //height: 250px;
.owl-carousel .owl-nav {
    bottom: initial;
    top: -100px;
    right: 10%;
    left: inherit; }

