.itemPage {
    .oneitem__body {
        //background: green;
        width: 100%; }
    .oneitem {
        &__title {
            text-transform: uppercase;
            font-size: 24px;
            margin-bottom: .5em; }
        &__gallery {
            float: left;
            width: 50%;
            @include sm {
                width: 100%; } }
        &__desc {
            float: right;
            width: 50%;
            @include sm {
                width: 100%;
                float: left; }
            dl {
                color: #949494;
                margin-bottom: .5em; }
            dt {
                float: left;
                width: 90px; }
            dd {
 }                //padding-left: 100px;
            .oneitem__price {
                color: #949494;
                font-size: 24px;
                margin: 1em 0;
                .bonus {
                    font-size: 16px; } }
            .btn {
                padding: .75em 1.25em;
                border: 1px solid #949494;
                border-radius: 5px;
                font-size: 18px;
                font-weight: normal;
                font-family: Gals;
                //background: #fff;
                padding-left: 40px;
                margin-bottom: .5em;
                &-incart {
                    background: #333 url('../img/basket.svg') no-repeat 10px 50%;
                    color: #fff;
                    border: none; }
                &-buyexpress {
                    background: url('../img/buy-express.svg') no-repeat 10px 50%; } } } } }
