body {
    background: #F2F2F2;
    font-family: Gals;
    //font-size: 18px;
    @include fs(16);
    //+f(Gals);
    //+css-lock(16, 50, 600, 1000);
    line-height: 1.4;

    a {
        color: #333;
        text-decoration: none;
        transition: .25s ease;
        &:hover {
            color: #000; } } }

h1, .h1 {
    font-size: 36px;
    font-weight: normal;
    .indexPage & {
        font-size: 48px;
        line-height: 36px; } }
.page_title {
 }    //margin-bottom: 2em;

p {
    //+fs(11);
    color: #949494;
    font-size: 16px; }
p+p {
    margin-bottom: 10px; }


//tabs
.tabs {
    @include clr;
    &__caption {
        @include clr;
        margin-bottom: .5em;
        border-bottom: 1px solid #ABABAB;
        li {
            float: left;
            color: #ABABAB;
            //border-bottom: 1px solid #ABABAB;
            font-size: 16px;
            text-transform: uppercase;
            padding: .5em 0;
            margin-right: 2em;
            cursor: pointer;
 } }            //padding-left: 1em;
    &__caption {
        li.active {
            color: #333;
            border-bottom: 2px solid #333;
            margin-bottom: -1px;
            font-weight: bold; } }
    &__content {
        @include clr;
        display: none;
        color: #949494; }

    &__content.active {
        display: block; } }

